<template>
  <div class="main" v-bind:class="{ active: $store.state.isActive }">
    <!-- <label for="locale">locale</label>
    <select v-model="locale">
      <option>en</option>
      <option>ja</option>
    </select>
    <p>message: {{ $t('hello') }}</p>-->
    <v-app>
      <guide axisV></guide>
      <header>
        <v-navigation-drawer
          disable-resize-watcher
          fixed
          :clipped="$vuetify.breakpoint.lgAndUp"
          v-model="sidebar"
          right
          app
          flat
          overflow
        >
          <Menus menuPosition="side" gray></Menus>
        </v-navigation-drawer>

        <v-toolbar
          class="nav"
          app
          flat
          :clipped-left="$vuetify.breakpoint.lgAndUp"
          v-bind:class="[$store.state.isDirect ? activeClass : '', errorClass]"
          height="64px"
        >
          <v-toolbar-title class="stateTitle">{{appName}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <Menus class="hidden-sm-and-down" menuPosition="top"></Menus>
          <v-toolbar-side-icon class="sidebar hidden-md-and-up" @click.stop="sidebar = !sidebar"></v-toolbar-side-icon>
        </v-toolbar>
      </header>
      <v-content>
        <v-container class="visual-container" v-bind:class="[$route.name]" ref="visualContainer">
          <!--test start-->
          <!--<router-view name="test"></router-view>-->
          <!--<router-view></router-view>-->
          <ul class="visual" ref="visual" v-if="isTest">
            <li
              v-for="n in 8"
              :key="n"
              class="block"
              v-bind:class="['block'+(n-1)]"
              v-bind:name="['block'+(n-1)]"
            >
            <v-btn color="accent" depressed to="/Test">{{n}}
            </v-btn>
            </li>
          </ul>
          <!--test end-->

          <ul class="visual" ref="visual" v-else>
            <li name="block0" class="block block0">
              <transition name="slide-right" appear>
                <keep-alive>
                  <router-view name="router0"></router-view>
                </keep-alive>
              </transition>
            </li>
            <li name="block1" class="block block1" v-bind:class="{ active: isLoad }">
              <router-view name="router1"></router-view>
            </li>
            <li name="block2" class="block block2" v-bind:class="{ active: isLoad }">
              <router-view name="router2"></router-view>
            </li>
            <li name="block3" class="block block3" v-bind:class="{ active: isLoad }">
              <router-view name="router3"></router-view>
            </li>
            <li name="block4" class="block block4">
              <transition name="zoom-out" appear>
                <keep-alive>
                  <router-view name="router4"></router-view>
                </keep-alive>
              </transition>
            </li>
            <li name="block5" class="block block5">
              <router-view name="router5"></router-view>
            </li>
            <li name="block6" class="block block6">
              <router-view name="router6"></router-view>
            </li>
            <li name="block7" class="block block7" v-bind:class="{ active: isLoad }">
              <router-view name="router7"></router-view>
            </li>
          </ul>

          <transition v-bind:name="transitionName" appear>
            <router-view name="detail" ref="detail"></router-view>
            <!--<component :is="layout">
              <router-view name="detail" ref="detail"></router-view>
            </component>-->
          </transition>

          <div class="sub-title" v-bind:class="{ active: isLoad }" ref="subTitle">
            <h2 class="title-top headline" v-bind:data-title="isTitle">{{$store.state.isTitle}}</h2>
            <p class="title-des caption">{{$store.state.isTitleSub}}</p>
            <ul class="colorItem-group" v-if="isTest">
              <li
                class="colorItem"
                v-for="item in colorItem"
                :key="item"
                v-bind:style="{ 'background-color': item, color:'#fff'}"
              >{{item}}</li>
            </ul>
          </div>
        </v-container>
      </v-content>

      <footers></footers>
    </v-app>
  </div>
</template>

<!-- <i18n>
{
  "en": {
    "hello": "hello world!"
  },
  "ko": {
    "hello": "안녕하세요！"
  },
  "ja": {
    "hello": "こんにちは、世界！"
  }
}
</i18n> -->

<script>
import Velocity from "velocity-animate";
import store from "./store";
import router from "./router";
import Menus from "@/components/Menus.vue";
import Footers from "@/components/Footer.vue";
import Guide from "@/components/Guide.vue";

const default_layout = 'default';
export default {
  components: {
    layout() {console.log('////',this.$route)
      return (this.$route.meta.layout || default_layout) + '-layout';
    },
    Menus,
    Footers,
    Guide
  },

  data() {
    return {
      isTest: false,
      isLoad: false,
      isEvent: false,
      isMobile: false,
      activeClass: "cancel-transition",
      errorClass: "",
      isActive: store.state.isActive,
      isDirect: store.state.isDirect,
      hasError: false,
      isTitle: store.state.isTitle,
      istitleSub: store.state.isTitleSub,
      appName: "HAEMILSOFT",
      sidebar: false,
      transitionName: "slide-top",
      locale: "en",
      colorItem: [
        "#4bb4e7",
        "#1e9cd9",
        "#0277bd",
        "#2f5aa8",
        "#242424",
        "#313131"
      ],
      blocks: [
        {
          speed: [640, 375, 375, 375, 375, 375, 375, 375],
          width: [1500, 375, 375, 375, 375, 375, 375, 375],
          height: [640, 320, 320, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#242424",
            "#242424",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 640, 640, 320, 375, 337, 375, 375],
          width: [375, 375, 375, 188, 375, 375, 375, 375],
          height: [640, 640, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#242424",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 375, 375, 320, 320, 320, 320, 375],
          width: [1125, 375, 375, 375, 1125, 375, 375, 375],
          height: [320, 320, 320, 320, 320, 320, 320, 160],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#313131",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 337, 640, 337, 337, 337, 337, 320],
          width: [750, 375, 375, 375, 375, 375, 375, 188],
          height: [640, 320, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#313131",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        }
      ],
      items: []
    };
  },
  beforeDestroy() {
    if (typeof window !== "undefined") {
      window.removeEventListener("resize", this.onResize, { passive: true });
    }
  },
  /* created () {
    // 뷰가 생성되고 데이터가 이미 감시 되고 있을 때 데이터를 가져온다.
    this.fetchData()
  }, */
  watch: {
    // 라우트가 변경되면 메소드를 다시 호출됩니다.
    // '$route': 'fetchData'
    /* locale (val) {
      this.$i18n.locale = val
    } */
  },
  mounted() {
    this.fn_resize();
    window.addEventListener("resize", this.onResize, { passive: true });

    const visual = this.$refs["visualContainer"].childNodes[0];
    const visualArray = this.fn_array(visual);
    let nameT = null;
    let nameF = null;
    let idx = null;

    router.beforeEach((to, from, next) => {
      idx = to.meta.index;
      this.isTitle = to.meta.title;
      this.isTitleSub = to.meta.titleSub;
      this.isLoad = true;
      /*
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      */
      if (to.path.split("/").length > 2) {
        this.transitionName = to.meta.transition;
        //console.log(visualArray[idx], idx)
        //console.log(this.blocks)
        //console.log(visualArray[idx].clientWidth)
        //this.$refs["detail"].clientWidth = visualArray[idx].clientWidth
        next();
        //this.fn_test(next, visualArray, idx);
      } else {
        const tr = this.transitionName.split('-');
        this.transitionName = tr[0];
        if(tr[1] == "top"){
          this.transitionName = this.transitionName+"-"+"bottom";
        }else if(tr[1] == "right"){
          this.transitionName = this.transitionName+"-"+"left";
        }else if(tr[1] == "bottom"){
          this.transitionName = this.transitionName+"-"+"top";
        }else if(tr[1] == "left"){
          this.transitionName = this.transitionName+"-"+"right";
        }else if(tr[1] == "in"){
          this.transitionName = this.transitionName+"-"+"out";
        }else if(tr[1] == "out"){
          this.transitionName = this.transitionName+"-"+"in";
        }else{
          this.transitionName = "fade"
        }
        this.fn_sub(next, this.blocks[idx], visualArray, idx);
      }
      
    });

    router.afterEach((to, from) => {
      //console.log('afterEach')
      this.isLoad = false;
    });
  },
  computed: {
    /*key() {console.log('hh')
      return this.$route.meta.key !== undefined 
      	? this.$route.meta.key 
        : this.$route
    }*/
  },
  methods: {
    fetchData() {
      // 데이터 가져오기 위한 유틸리티/API 래퍼로 변경합니다.
      console.log("fetchData");
    },
    fn_array(elements) {
      let arr = [];
      for (var i = 0; i < elements.childNodes.length; i++) {
        arr[i] = elements.childNodes[i];
      }
      return arr;
    },
    fn_resize() {
      this.isMobile = window.innerWidth < 960; // console.log(this.isMobile)
    },
    fn_test: function(next, visualArray, idx) {
      console.log(
        "fn_test",
        visualArray[idx].clientWidth,
        visualArray[idx].clientHeight,
        visualArray[idx].offsetX
      );
      //visualArray[idx].classList.add("anotherclass")
    },
    fn_sub: function(next, blockSize, visual, idx) {
      //this.fn_size(visual, blockSize, 0, next);
      //const loop = this.fn_test();
      const vLangth = visual.length - 1;
      visual.forEach(function(element, index) {
        //element.childNodes[0].style.display = "hidden";
        //setTimeout(function () {

        Velocity(
          element,
          {
            width: blockSize.width[index],
            height: blockSize.height[index],
            translateX: blockSize.x[index],
            translateY: blockSize.y[index],
            "background-color": blockSize.background[index]
          },
          {
            //delay: index * 30,
            duration: 300, // blockSize.speed[index],
            easing: [0.215, 0.61, 0.355, 1],
            easing: [0.4, 0.0, 0.2, 1],
            complete: function() {
              //element.removeAttribute('style');
              if (index == vLangth || idx == 0) {
                next();
              }
            }
          }
        );

        //}, 0)
      });
    }
  }
};
</script>

<style lang="scss">
.colorItem-group {
  list-style: none;
  padding: 0;
  margin: 0;
  transform: rotate(90deg) translateX(-2px) translateY(-657px);
  display: flex;
  flex-direction: row;
  transform-origin: left top;
  .colorItem {
    color: #fff;
    font-size: 11px;
    width: 62.5px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.sub-title {
  position: absolute;
  width: auto;
  height: 64px;
  left: 50%;
  top: 50%;
  z-index: 10;
  transform-origin: 0%;
  transform: rotate(-90deg) translateX(-288px) translateY(-782px);
  /*transform: rotate(-90deg) translateX(-268px) translateY(-410px);*/
}

.display-1,
.headline,
.caption {
  padding: 0px;
}

.title-top {
  position: relative;
  display: inline-block;
  color: #1e9cd9;
}
.title-top:before {
  /*transition: all 320ms cubic-bezier(0.25, 0.8, 0.5, 1);*/
  background-color: #ffffff;

  transition-property: left, width;
  transition-duration: 0.2s, 0.4s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1),
    cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.title-des {
  position: relative;
  display: block;
  color: #242424;
  height: 20px;
  text-decoration: none !important;
  margin-top: 2px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #0277bd;
  color: #fff;
}
.title-des:before {
  /*transition: all 400ms cubic-bezier(0.25, 0.8, 0.5, 1);*/
  background-color: #0277bd;
  transition-property: left, width;
  transition-duration: 0.3s, 0.3s;
  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94),
    cubic-bezier(0.215, 0.61, 0.355, 1);
}

.title-top:before,
.title-des:before {
  content: attr(data-title);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 100%;
  z-index: 1;

  width: 0%;

  overflow: hidden;
  color: transparent;
}
.sub-title.active .title-top:before,
.sub-title.active .title-des:before {
  width: 100%;
}
</style>
