router.beforeEach((to, from, next) => {
  if (to.name) {
    // Start the route progress bar.
    // NProgress.start()
  }
  next()
})

router.afterEach((to, from) => { // console.log(to.name, '/1/ ', from.name)
  // Complete the animation of the route progress bar.
  let act = false
  let dic = false
  if (to.name === 'Home' || to.name === 'Landing' || to.name === 'PageNotFound') {
    act = true
  }
  if (from.name == null) {
    dic = true
  }
  store.commit('isActive', {
    direct: dic,
    active: act,
    title: to.meta.title,
    titleSub: to.meta.titleSub,
    titleComment: to.meta.titleComment
  })
  // console.log(store.state.isDirect)
  // console.log('to: ',to.name, ' / from: ', from.name, ' / isActive: ', store.state.isActive)
  // NProgress.done()
})

export default router
