import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    isDirect: false,
    isActive: false,
    isTitle: '',
    isTitleSub: '',
    isTitleComment: '',
    isEvent: ''
  },
  getters: {

  },
  mutations: {
    // increment: state => state.isActive = true,
    // decrement: state => state.isActive = false,
    isActive (state, payload) {
      state.isDirect = payload.direct
      state.isActive = payload.active
      state.isTitle = payload.title
      state.isTitleSub = payload.titleSub
      state.isTitleComment = payload.titleComment
    },
    isEvent (state, payload) {
      state.isEvent = payload.event
    }
  },
  actions: {

  }
})
